import { Injectable } from '@angular/core';

import { Menu, BuyNow, Footer } from '../../constants';
import { TranslatorService } from './translator.service';

@Injectable({
  providedIn: 'root'
})
export class MenuService {

  private _translator: TranslatorService;

  private currentLang: string;
  private menuItems: Array<any>;
  private buyItems: Array<any>;
  private footerItems: Array<any>;

  constructor(
    translator: TranslatorService
  ) { 
    this._translator = translator;
    this.currentLang = '';
    this.menuItems = [];
    this.buyItems = [];
    this.footerItems = [];
  }

  private createMenu(items: Array<{
    translate: string,
    link?: string,
    external?: boolean,
    target?: string,
    icon?: string,
    submenu?: Array<any>,
    active?: boolean,
    order?: any
  }>, order: Array<number>, lang?: string) {

    let sortedMenu: Array<any> = [];

    order.forEach((index) => {
      const item = items[index];
      if (item.submenu && item.order) {
        item.submenu = this.createMenu(item.submenu, item.order[lang]);
      }
      if (item.link) {
        item.link = item.link.replace(':lang', this._translator.getCurrentLanguage())
      }

      sortedMenu.push(item);
    });

    return sortedMenu;
  }

  getMenu(lang: string) {
    if (this.menuItems.length === 0 || lang !== this.currentLang) {
      this.currentLang = lang;
      this.menuItems = this.createMenu(Menu.items, Menu.order[lang], lang);
    }

    return this.menuItems;
  }

  getBuyNow(lang: string) {
    if (this.buyItems.length === 0 || lang !== this.currentLang) {
      this.currentLang = lang;
      this.buyItems = this.createMenu(BuyNow.items, BuyNow.order[lang], lang);
    }

    return this.buyItems;
  }

  getFooter(lang: string) {
    if (this.footerItems.length === 0 || lang !== this.currentLang) {
      this.currentLang = lang;
      this.footerItems = this.createMenu(Footer.items, Footer.order[lang], lang);
    }

    return this.footerItems;
  }
}
